import { configureStore, createSlice } from "@reduxjs/toolkit";
import user from "./store/userSlice";

let cart = createSlice({
  name: "cart",
  initialState: [
    { id: 0, name: "White and Black", count: 2 },
    { id: 2, name: "Grey Yordan", count: 1 },
  ],
  reducers: {
    quantityUpdate(state, action) {
      let findItem = state.find((item) => item.id == action.payload);
      findItem.count = parseInt(findItem.count) + 1;
    },
    addToCart(state, action) {
      /* state = [...state, action.payload]; */
      let isItemExist = state.findIndex((index) => {
        return index.id == action.payload.id;
      });
      if (isItemExist > -1) {
        state[isItemExist].count++;
      } else {
        state.push(action.payload);
      }
    },
    deleteCart(state, action) {
      let deleteItem = state.findIndex((item) => {
        return item.id == action;
      });
      state.splice(deleteItem, 1);
    },
  },
});

// 1. 

// 2.addToCart      1 .

let stock = createSlice({
  name: "stock",
  initialState: [10, 11, 12],
});

export default configureStore({
  reducer: {
    user: user.reducer,
    stock: stock.reducer,
    cart: cart.reducer,
  },
});

export let { quantityUpdate, addToCart, deleteCart } = cart.actions;

